.sidebar-title{
  font-size: 22px;
  font-weight: normal;
  margin-bottom: 20px;
}

.sidebar-item{
  margin-top: 20px;
}

.sidebar-label-container {
  display: block;
  position: relative;
  padding-left: 35px;
  margin-bottom: 12px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.sidebar-label-container input{
  position: absolute;
  opacity: 0;
  cursor: pointer;
}

.chacKmark {
  position: absolute;
  top: 0px;
  left: 0;
  height: 20px;
  width: 20px;
  background-color: #eee;
  border-radius: 50%;
}

.sidebar-label-container:hover input ~ .chacKmark{
  background-color: #ccc;
}

.sidebar-label-container input:checked ~ .chacKmark{
  background-color: #ae0e0e;
}

.chacKmark::after{
  content: "";
  position: absolute;
  display: none ;
}

.sidebar-label-container input:checked ~ .chacKmark::after{
  display: block;
}

.sidebar-label-container .chacKmark::after{
  top: 6.4px;
  left: 6.4px;
  width: 7px;
  height: 7px;
  border-radius: 50%;
  background: white;
}

.line{
  margin-top: 3rem;
  border-color: #f7f7f7;
}